import React, { useEffect, useRef } from "react";
import dropImage from "../assets/drop/drop.png";
import rainSound from "../sounds/rain.mp3";

export default function RainBackground({ mode }) {
  const canvasRef = useRef();
  const audioRef = useRef();
  const startedRef = useRef(false); // مطمئن می‌شویم صدا یک بار اجرا شود

  useEffect(() => {
    const canvas = canvasRef.current;
    const ctx = canvas.getContext("2d");
    let width = (canvas.width = window.innerWidth);
    let height = (canvas.height = window.innerHeight);

    const dropImg = new Image();
    dropImg.src = dropImage;

    const drops = Array.from({ length: 250 }, () => ({
      x: Math.random() * width,
      y: Math.random() * height,
      speed: Math.random() * 3 + 0.5,
      scale: Math.random() * 0.08 + 0.0006,
    }));

    function animate() {
      ctx.clearRect(0, 0, width, height);

      drops.forEach((d) => {
        if (dropImg.complete) {
          const w = dropImg.width * d.scale;
          const h = dropImg.height * d.scale;
          ctx.drawImage(dropImg, d.x, d.y, w, h);
        }
        d.y += d.speed;
        if (d.y > height) {
          d.y = -10;
          d.x = Math.random() * width;
        }
      });

      requestAnimationFrame(animate);
    }

    animate();

    const handleResize = () => {
      width = canvas.width = window.innerWidth;
      height = canvas.height = window.innerHeight;
    };
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, [mode]);

  // پخش صدا بعد از تعامل کاربر
  useEffect(() => {
    const playAudio = () => {
      if (!startedRef.current && audioRef.current) {
        audioRef.current.volume = mode === "dark" ? 0.5 : 0.3;
        audioRef.current.play().catch(() => console.log("Autoplay blocked"));
        startedRef.current = true;
      }
    };

    window.addEventListener("click", playAudio); // هر کلیک صدا شروع شود
    window.addEventListener("touchstart", playAudio); // برای موبایل

    return () => {
      window.removeEventListener("click", playAudio);
      window.removeEventListener("touchstart", playAudio);
    };
  }, [mode]);

  return (
    <>
      <canvas
        ref={canvasRef}
        style={{
          position: "fixed",
          top: 0,
          left: 0,
          width: "100%",
          height: "100%",
          zIndex: -6,
          pointerEvents: "none",
        }}
      />
      <audio ref={audioRef} src={rainSound} loop />
    </>
  );
}
