import React, { useMemo, useRef, useState, useEffect } from "react";
import { BrowserRouter as Router, Routes, Route, Link, useParams, Navigate, useNavigate } from "react-router-dom";
import { Container, Typography, Box, AppBar, Toolbar, CssBaseline, Button, Grid, IconButton, Tooltip, Divider } from "@mui/material";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import { useTranslation } from "react-i18next";
import { AnimatePresence, motion, useScroll, useTransform } from "framer-motion";
import RainBackground from "./components/RainBackground";
import logo from "./assets/logo.jpg";
import logo2 from "./assets/logo2.jpg";
import { FaInstagram, FaTwitter, FaFacebook, FaLinkedin } from "react-icons/fa";
import "./i18n";

// ===============================
// ثابت‌ها و داده‌ها
// ===============================
const BRAND = "moonsa company";

const dronesList = [
  { id: "drone1", name: "MOONSA FED1" },
  { id: "drone2", name: "MOONSA XF-47" },
  { id: "drone3", name: "MOONSA XF-44 Plus" },
  { id: "drone4", name: "MOONSA XF44" },
  { id: "drone5", name: "MOONSA Aryo FF One" },
  { id: "drone6", name: "MOONSA F48 Plus" },
];

const sixthGenDrones = [
  { id: "sg-drone1", name: "MOONSA SGX-900" },
  { id: "sg-drone2", name: "MOONSA SGX-920" },
  { id: "sg-drone3", name: "MOONSA SG Ultimate" },
  { id: "sg-drone4", name: "MOONSA Hyper-X" },
];

// بارگذاری تصاویر
const importAllImages = (r) => {
  const images = {};
  r.keys().forEach((key) => {
    const parts = key.split("/");
    const id = parts[1];
    if (!images[id]) images[id] = [];
    images[id].push(r(key));
  });
  return images;
};
const droneImages = importAllImages(require.context("./assets/images", true, /\.jpg$/));

// ===============================
// کامپوننت‌ها
// ===============================
const MotionBox = motion(Box);

// انیمیشن عمومی
const fadeUp = {
  initial: { opacity: 0, y: 24 },
  whileInView: { opacity: 1, y: 0 },
  viewport: { once: true, margin: "-80px" },
  transition: { duration: 0.6, ease: "easeOut" },
};

// ModeSwitch با افکت موجی
function ModeSwitch({ mode, setMode, label }) {
  const [pulse, setPulse] = useState(false);
  return (
    <Tooltip title={label}>
      <Button
        onClick={() => {
          setPulse(true);
          setTimeout(() => setPulse(false), 500);
          setMode((prev) => (prev === "dark" ? "light" : "dark"));
        }}
        sx={{
          position: "relative",
          backgroundColor: "#d4af37",
          color: mode === "dark" ? "#000" : "#222",
          fontWeight: "bold",
          borderRadius: 999,
          px: 2.5,
          '&:hover': { backgroundColor: "#bfa237" },
          overflow: "hidden",
        }}
      >
        {mode === "dark" ? "Light" : "Dark"}
        {pulse && (
          <motion.span
            initial={{ scale: 0, opacity: 0.5 }}
            animate={{ scale: 3, opacity: 0 }}
            transition={{ duration: 0.5, ease: "easeOut" }}
            style={{
              position: "absolute",
              inset: 0,
              borderRadius: "50%",
              background: "radial-gradient(rgba(255,255,255,.5), transparent)",
              pointerEvents: "none",
            }}
          />
        )}
      </Button>
    </Tooltip>
  );
}

// کارت پهپاد
function DroneCard({ drone, mode, gold }) {
  return (
    <motion.div
      initial={{ opacity: 0, y: 30 }}
      whileInView={{ opacity: 1, y: 0 }}
      viewport={{ once: true }}
      transition={{ duration: 0.5 }}
    >
      <Box
        component={Link}
        to={`/drones/${drone.id}`}
        sx={{
          display: "block",
          textDecoration: "none",
          border: "1px solid rgba(255,215,55,0.25)",
          borderRadius: 3,
          p: 3,
          textAlign: "center",
          background: mode === "dark" ? "rgba(0,0,0,0.25)" : "rgba(255,255,255,0.35)",
          backdropFilter: "blur(10px) saturate(180%)",
          boxShadow: "0 8px 30px rgba(0,0,0,0.25)",
          transition: "transform .3s ease, box-shadow .3s ease",
          cursor: "pointer",
          '&:hover': {
            transform: "translateY(-6px) scale(1.03)",
            boxShadow: `0 0 30px ${gold ? "#d4af37" : "#6a0dad"}`,
          },
        }}
      >
        <Typography variant="h6" gutterBottom sx={{ fontWeight: 700, color: gold ? "#d4af37" : "#6a0dad" }}>
          {drone.name}
        </Typography>
        <Typography variant="body2" sx={{ opacity: .8 }}>
          View details →
        </Typography>
      </Box>
    </motion.div>
  );
}

// Typewriter افکت
function Typewriter({ phrases, color = "#d4af37", interval = 120 }) {
  const [text, setText] = useState("");
  const [index, setIndex] = useState(0);
  const [subIndex, setSubIndex] = useState(0);
  const [forward, setForward] = useState(true);

  useEffect(() => {
    const timeout = setTimeout(() => {
      if (forward) {
        if (subIndex < phrases[index].length) setSubIndex(subIndex + 1);
        else setForward(false);
      } else {
        if (subIndex > 0) setSubIndex(subIndex - 1);
        else {
          setForward(true);
          setIndex((prev) => (prev + 1) % phrases.length);
        }
      }
      setText(phrases[index].substring(0, subIndex));
    }, interval);
    return () => clearTimeout(timeout);
  }, [subIndex, index, forward, phrases, interval]);

  return (
    <Typography
      variant="h5"
      sx={{ color, mt: 1, opacity: .9, fontWeight: 500, letterSpacing: 1.2 }}
    >
      {text}<span style={{ borderRight: `2px solid ${color}`, marginLeft: 2 }} />
    </Typography>
  );
}

// Hero با پارالاکس
function Hero({ mode }) {
  const { scrollY } = useScroll();
  const ySlow = useTransform(scrollY, [0, 600], [0, -60]);
  const yFast = useTransform(scrollY, [0, 600], [0, -120]);
  const scaleTitle = useTransform(scrollY, [0, 300], [1, 0.92]);
  const navigate = useNavigate();

  return (
    <Box sx={{ position: "relative", height: { xs: 420, md: 520 }, overflow: "hidden", borderBottom: "1px solid rgba(255,215,55,0.2)" }}>
      <motion.div style={{ position: "absolute", inset: 0, y: yFast }}>
        <Box sx={{ width: "100%", height: "100%", background: mode === "dark"
          ? "radial-gradient(1200px 520px at 20% 20%, rgba(212,175,55,.25), transparent), radial-gradient(1000px 520px at 80% 80%, rgba(106,13,173,.2), transparent)"
          : "radial-gradient(1200px 520px at 20% 20%, rgba(212,175,55,.35), transparent), radial-gradient(1000px 520px at 80% 80%, rgba(106,13,173,.18), transparent)" }} />
      </motion.div>

      <motion.div style={{ position: "absolute", top: 24, left: 24, y: ySlow }}>
        <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
          <Box component="img" src={logo} alt="logo" sx={{ height: 52, borderRadius: 2 }} loading="lazy" />
          <Box component="img" src={logo2} alt="logo2" sx={{ height: 72, borderRadius: 2 }} loading="lazy" />
        </Box>
      </motion.div>

      <motion.div style={{ position: "absolute", inset: 0, display: "flex", alignItems: "center", justifyContent: "center", scale: scaleTitle }}>
        <Box sx={{ textAlign: "center", px: 2 }}>
          <motion.h1 initial={{ opacity: 0, y: 20 }} animate={{ opacity: 1, y: 0 }} transition={{ duration: .8 }}
            style={{ margin: 0, fontSize: "clamp(36px,6vw,64px)", letterSpacing: 2, color: "#d4af37", textShadow: "0 0 20px rgba(212,175,55,.45)", fontFamily: "Cinzel, serif" }}>
            {BRAND.toUpperCase()}
          </motion.h1>

          <Typewriter phrases={["Innovation in the skies...", "Redefining Drone Technology", "Luxury Autonomous Drones"]} />

          <Box sx={{ mt: 3, display: "flex", gap: 1, justifyContent: "center" }}>
            <Button variant="contained" onClick={() => document.getElementById("drones-grid")?.scrollIntoView({ behavior: "smooth" })}>
              Explore Drones
            </Button>
            <Button variant="outlined" onClick={() => navigate("/drones/sg-drone1")}>Sixth Gen Flagship</Button>
          </Box>
        </Box>
      </motion.div>
    </Box>
  );
}

// Footer مدرن
function Footer() {
  return (
    <Box sx={{ mt: 10, py: 6, background: "rgba(0,0,0,0.15)", backdropFilter: "blur(10px) saturate(180%)", textAlign: "center", color: "#fff" }}>
      <Typography variant="h6" sx={{ mb: 2, fontWeight: "bold", color: "#d4af37" }}>Connect with us</Typography>
      <Box>
        {[FaInstagram, FaTwitter, FaFacebook, FaLinkedin].map((Icon, i) => (
          <IconButton key={i} sx={{ color: "#d4af37", mx: 1, transition: "0.3s", "&:hover": { color: "#fff", transform: "scale(1.3)" } }}>
            <Icon size={24} />
          </IconButton>
        ))}
      </Box>
      <Typography sx={{ mt: 3, fontSize: "0.9rem", color: "#d4af37" }}>
        © 2025-2035 Sajad Feyz Abadi (Sully Armstrong) - MOONSA
      </Typography>
    </Box>
  );
}

// صفحه اصلی
function Home({ mode }) {
  const { t, i18n } = useTranslation();
  const changeLanguage = (lng) => i18n.changeLanguage(lng);

  return (
    <Container sx={{ mt: 4 }}>
      <Hero mode={mode} />

      <MotionBox id="drones-grid" {...fadeUp} sx={{ mt: 6 }}>
        <Typography variant="h5" gutterBottom sx={{ textAlign: "center", fontWeight: 700 }}>
          {t("drones")}
        </Typography>
        <Grid container spacing={3}>
          {dronesList.map((drone) => (
            <Grid item xs={12} sm={6} md={4} key={drone.id}>
              <DroneCard drone={drone} mode={mode} />
            </Grid>
          ))}
        </Grid>
      </MotionBox>

      <MotionBox {...fadeUp} sx={{ mt: 8 }}>
        <Typography variant="h4" gutterBottom sx={{ fontWeight: 800, textAlign: "center", color: "#d4af37", textShadow: "0 0 15px rgba(212,175,55,.4)" }}>
          Sixth Generation Special Drones
        </Typography>
        <Grid container spacing={3}>
          {sixthGenDrones.map((drone) => (
            <Grid item xs={12} sm={6} md={4} key={drone.id}>
              <DroneCard drone={drone} mode={mode} gold />
            </Grid>
          ))}
        </Grid>
      </MotionBox>

      {/* تماس و زبان */}
      <MotionBox {...fadeUp} sx={{ mt: 3, textAlign: "center" }}>
        <Typography variant="h5" gutterBottom>{t("contact")}</Typography>
        <Typography sx={{ opacity: .85 }}>@Space_sf / @perple_XCH / sullyarmstrong03@gmail.com</Typography>
        <Box mt={2}>
          <Button variant="contained" onClick={() => changeLanguage("en")}>EN</Button>
          <Button variant="contained" onClick={() => changeLanguage("fa")} sx={{ ml: 1 }}>FA</Button>
        </Box>
      </MotionBox>

      <Footer />
    </Container>
  );
}

// جزئیات پهپاد
function DroneDetail({ mode }) {
  const { t } = useTranslation();
  const { droneId } = useParams();
  const allDrones = [...dronesList, ...sixthGenDrones];
  const drone = allDrones.find((d) => d.id === droneId);

  useEffect(() => { window.scrollTo({ top: 0, behavior: "smooth" }); }, [droneId]);

  if (!drone) return (
    <Container sx={{ mt: 6 }}>
      <Typography variant="h5">{t("drone_not_found")}</Typography>
      <Footer />
    </Container>
  );

  const images = droneImages[droneId] || [];

  return (
    <Container sx={{ mt: 4 }}>
      <MotionBox {...fadeUp}>
        <Typography variant="h4" gutterBottom sx={{ fontWeight: 800, textAlign: "center", color: "#d4af37", textShadow: "0 0 10px rgba(212,175,55,.35)" }}>
          {drone.name}
        </Typography>
      </MotionBox>

      <Grid container spacing={2} justifyContent="center">
        {images.map((src, i) => (
          <Grid item key={i}>
            <motion.img
              src={src}
              alt={`${drone.name} ${i + 1}`}
              loading="lazy"
              style={{ width: 240, height: 168, objectFit: "cover", borderRadius: 16, border: "1px solid rgba(255,215,55,0.25)", boxShadow: "0 8px 30px rgba(0,0,0,0.25)" }}
              initial={{ opacity: 0, y: 10 }}
              whileInView={{ opacity: 1, y: 0 }}
              viewport={{ once: true }}
              transition={{ duration: .4, delay: i * 0.05 }}
            />
          </Grid>
        ))}
      </Grid>

      <MotionBox {...fadeUp} sx={{ mt: 3, border: "1px solid rgba(255,215,55,0.25)", borderRadius: 3, p: 3, background: mode === "dark" ? "rgba(0,0,0,0.25)" : "rgba(255,255,255,0.35)", backdropFilter: "blur(15px) saturate(180%)", boxShadow: "0 8px 30px rgba(0,0,0,0.25)" }}>
        <Typography variant="body1" sx={{ fontSize: 18, lineHeight: 1.7, whiteSpace: "pre-line", opacity: .95 }}>
          {t(`drone_details.${droneId}`, t("description_unavailable"))}
        </Typography>
      </MotionBox>

      <Footer />
    </Container>
  );
}

// ===============================
// برنامه اصلی
// ===============================
export default function App() {
  const { t } = useTranslation();
  const [mode, setMode] = useState("dark");

  const theme = useMemo(() => createTheme({
    palette: {
      mode,
      primary: { main: "#d4af37" },
      secondary: { main: "#6a0dad" },
      background: { default: mode === "dark" ? "#0b0b0b" : "#f5f5f5", paper: mode === "dark" ? "#0b0b0b" : "#ffffff" },
      text: { primary: mode === "dark" ? "#d4af37" : "#222", secondary: mode === "dark" ? "#d4af37" : "#555" },
    },
    typography: { fontFamily: "Roboto, Arial, sans-serif" }
  }), [mode]);

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Router>
        <RainBackground mode={mode} />
        <AppBar position="fixed" color="transparent" elevation={0}>
          <Toolbar sx={{ justifyContent: "flex-end", gap: 2 }}>
            <ModeSwitch mode={mode} setMode={setMode} label="Toggle Dark/Light Mode" />
          </Toolbar>
        </AppBar>

        <Box sx={{ mt: 8 }}>
          <Routes>
            <Route path="/" element={<Home mode={mode} />} />
            <Route path="/drones/:droneId" element={<DroneDetail mode={mode} />} />
            <Route path="*" element={<Navigate to="/" />} />
          </Routes>
        </Box>
      </Router>
    </ThemeProvider>
  );
}

